import BigNumber from "bignumber.js"
/**
 * showdoc
 * @catalog JS/MathUtil
 * @title 
 * @description 
 * @method static
 * @url import { numberPlus } from '@/utils/MathUtil.js'
 * @param nb1  Number 1
 * @param nb1  Number 2
 * @return Number  
 */
export function numberPlus(nb1, nb2) {
  let v1 = new BigNumber(nb1);
  let v2 = new BigNumber(nb2);
  return v1.plus(v2).toNumber();
}



