/**
 * Jssql group by
 * @author cls
 * @date 2019-04-26
 */
import {
  numberPlus
} from '@/utils/MathUtil.js'

/**
 * showdoc
 * @catalog JS/DataGroup
 * @title 
 * @description group
 * @method static
 * @url import { dataGroupBy } from '@/funclib/DataGroup.js'
 * @param datas  [{},{}] 
 * @param group  {groupBy:[],calcFields:[]} 
 * @param group.groupBy  groupBy:['f1','f2'] 
 * @param group.calcFields  calcFields:[{name:'value',summaryType:'count'}]} 
 * @param calcFields[0].name  {name:'value'} 
 * @param calcFields[0].summaryType  {summaryType:'count'} [count(), sum(), max(), min(), avg(),concat()]
 * @return [{'f1':'v1','f2':'v2'}]
 * @return_param f1 string 
 * @return_param v1 object 
 * @remark 
 *  dataGroupBy(
 *    [{'userCity':'','userId':''},{'userCity':'','userId':''},{'userCity':'','userId':''}],
 *    {groupBy:["userCity"], calcFields:[{name:'value',title:'',summaryType:'count'}]}
 *  )
 *  ==>
 *  [{"userCity":"","value":2},{"userCity":"","value":1}]
 */
export function dataGroupBy(datas, group) {  
  var groupJson = {};
  var groupKeys = [];
  datas.forEach(data => {
    const groupKey = dataGroupKey(data, group.groupBy)
    if (!groupJson[groupKey]) {
      groupJson[groupKey] = {}
      groupKeys.push(groupKey);
    }
    dataCalcRecd(groupJson[groupKey], group.calcFields, data)
  })

  var groupRecds = []
  for (var i = 0, l = groupKeys.length; i < l; i++) {
    var gKey = groupKeys[i];
    var groupRecd = dataGroupRecd(gKey, group.groupBy)
    var recd = {
      ...groupRecd,
      ...groupJson[gKey]
    }
    groupRecds.push(recd)
  }
  return groupRecds
}

/**
 * key,  $$ 
 * @param {*} data {f1:'v1',f2:'v2'}  
 * @param {*} groupBy  ['f1','f2']
 * @param {*} splitChar   $$
 */
export function dataGroupKey(data, groupBy, splitChar) {
  splitChar = splitChar || '$$'
  let key = ''
  groupBy.forEach((fn, i) => {
    key += (i === 0 ? '' : splitChar) + (data[fn] || 'NULL')
  })
  return key
}

/**
 * groupKey[{},{}]
 * @param {*} groupKey  $$
 * @param {*} groupBy  ['f1','f2']
 * @param {*} splitChar   $$
 */
export function dataGroupRecd(groupKey, groupBy, splitChar) {
  splitChar = splitChar || '$$'
  var groupRecd = {}
  const groupVals = groupKey.split(splitChar)
  groupBy.forEach((fn, i) => {
    if (!(groupVals[i] === 'NULL')) {
      groupRecd[fn] = groupVals[i]
    }
  })
  return groupRecd
}

/**
 * 
 * @param {*} data {f1:'v1',f2:'v2'}  
 * @param {*} calcFields [{name:'value',summaryType:'count'}] 
 */
export function dataCalcRecd(data, calcFields, row) {
  calcFields.forEach(calcField => {
    const fn = calcField.name
    if (calcField.summaryType === 'count') {
      data[fn] = (data[fn] || 0) + 1
    }
    if (calcField.summaryType === 'sum') {
      data[fn] = (data[fn] || 0)
      if (typeof data[fn] == "number") {
        // data[fn] = row[fn] + data[fn];
        data[fn] = numberPlus(row[fn], data[fn])
      }
    }
    // {fields:['f1','f2'],split:'.'}
    if (calcField.summaryType === 'concat') {
      if (calcField.fields && calcField.fields.length > 0) {
        if (!data[fn]) {
          calcField.fields.forEach((f, i) => {
            if (i > 0) {
              data[fn] += calcField.split || ",";
            }
            data[fn] = (data[fn] || "") + row[f];
          })
        }
      } else {
        console.error("" + JSON.stringify(calcField) + ", fields")
      }
    }
  })
}



