import {
  dataGroupKey
} from "@/funclib/DataGroup.js";

/**
 * showdoc
 * @catalog JS/DataTree
 * @title 
 * @description 
 * @method static
 * @url import { groupToTreeData } from '@/utils/DataTree.js'
 * @param datas  [{},{}] ,
 * @param tree  {groupBy:['f1','f2'],childNodeKeyField:'methodName',childrenKey:'children'} 
 * @param tree.groupBy  groupBy:['f1','f2'] 
 * @param tree.childNodeKeyField  'methodName' id
 * @param tree.parentLabelField  'labelFieldName' 
 * @param tree.childLabelField  'labelFieldName' 
 * @param tree.childrenKey  'children' key
 * @param tree.nodeKey  'treeId' Id
 * @return [{'f1':'v1','f2':'v2'}]
 * @return_param f1 string 
 * @return_param v1 object 
 * @remark 
 *  groupToTreeData(
 *    [{"methodName":"abs","className":"MathFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib"},{"methodName":"directSuperiors","className":"OrgFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib"},{"methodName":"curtOperator","className":"OrgFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib"},{"methodName":"plus","className":"MathFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib"},{"methodName":"curtDate","className":"DateFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib"},{"methodName":"curtDateTime","className":"DateFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib"}],
 *    {groupBy:["refClassImport","className"],parentLabelField:'classNameCn',childLabelField:'methodNameCn',childrenKey:"children"}
 *  )
 *  ==>
 *  [{"methodName":"abs","className":"MathFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib","label":"","children":[{"methodName":"abs","className":"MathFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib","label":""},{"methodName":"plus","className":"MathFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib","label":""}]},{"methodName":"directSuperiors","className":"OrgFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib","label":"","children":[{"methodName":"directSuperiors","className":"OrgFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib","label":""},{"methodName":"curtOperator","className":"OrgFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib","label":""}]},{"methodName":"curtDate","className":"DateFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib","label":"","children":[{"methodName":"curtDate","className":"DateFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib","label":""},{"methodName":"curtDateTime","className":"DateFunc","classNameCn":"","methodNameCn":"","refClassImport":"com.zhc.eap.funclib","label":""}]}]
 */
export function groupToTreeData(datas, tree) {
  let nodeKey = tree.nodeKey || "treeId"
  let childrenKey = tree.childrenKey || "children";
  var groupJson = {};
  datas.forEach(data => {
    const groupKey = dataGroupKey(data, tree.groupBy, ".")
    if (!groupJson[groupKey]) {
      groupJson[groupKey] = {};
      groupJson[groupKey][childrenKey] = [];
    }

    let treeNode = {
      label: data[tree.childLabelField]
    }
    treeNode[nodeKey] = groupKey + "." + data[tree.childNodeKeyField];
    treeNode = {
      ...treeNode,
      ...data
    };
    groupJson[groupKey][childrenKey].push(treeNode)
  })

  var treeRecds = []
  for (var gKey in groupJson) {
    var groupRecd = groupJson[gKey];
    var firstChild = groupJson[gKey][childrenKey][0];
    let treeNode = {
      label: firstChild[tree.parentLabelField]
    }
    treeNode[nodeKey] = gKey;

    var recd = {
      ...firstChild,
      ...treeNode,
      ...groupRecd
    }
    treeRecds.push(recd)
  }
  return treeRecds
}

/**
 * 
 * 
 * 
 */
export function convertToTreeData(data, tree) {
  tree = tree || {
    id: "id",
    parentId: "parentId",
    rootValue: "-1"
  }
  //
  let parents = data.filter(value => value[tree.parentId] == tree.rootValue || value[tree.parentId] == 'undefined' || value[tree.parentId] == null)

  //
  let children = data.filter(value => value[tree.parentId] !== tree.rootValue && value[tree.parentId] !== 'undefined' && value[tree.parentId] != null)

  //
  let translator = (parents, children) => {
    //
    parents.forEach((parent) => {
      //
      children.forEach((current, index) => {
        //
        if (current[tree.parentId] === parent[tree.id]) {
          //
          let temp = JSON.parse(JSON.stringify(children))
          //temptemp
          temp.splice(index, 1)
          //
          translator([current], temp)
          //children
          typeof parent.children !== 'undefined' ? parent.children.push(current) : parent.children = [current]
        }
      }
      )
    }
    )
  }

  //
  translator(parents, children)

  //
  return parents
}

/**
 * 
 * @param {Node[]} datas 
 * @param {recd => boolean} filterFunc 
 * @return 
 */
export function filterTreeData(datas, filterFunc) {
  // 
  if (!(datas && datas.length)) {
    return [];
  }

  const newChildren = [];
  for (const recd of datas) {
    if (filterFunc(recd)) {
      // 
      newChildren.push(recd);
      recd.children = filterTreeData(recd.children, filterFunc);
    } else {
      // 
      // 
      newChildren.push(...filterTreeData(recd.children, filterFunc));
    }
  }
  return newChildren;
}



