/**
 * 
 */
export default {
    data() {
        return {
            //
            slots: []
        }
    },
    methods: {
        // 
        slotChildren(slotId) {
            let rest = [];
            (this.conf.children || []).forEach(child => {
                if (child.slot == slotId) {
                    rest.push(child);
                }
            });
            return rest;
        }
    }
}



